import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { Storage } from "@ionic/storage";
import { NavController, NavParams, Events } from "ionic-angular";
import { FormBuilder } from "@angular/forms";


@Component({
  selector: "page-edit-note",
  templateUrl: "edit-note.html"
})
export class EditNotePage {
  note: any;
  user_id: any;
  content: string;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public storage: Storage,
    public http: HttpClient,
    private formBuilder: FormBuilder,
    public events: Events
  ) {
    this.note = this.navParams.get("newnote");
    this.content = this.note.content;
  }

  saveNote() {
    this.note.content = this.content;
    this.updateNote();
    this.events.publish("note:edited", this.note);
    this.navCtrl.getViews().forEach(element => {
      if (element.name == "NotesPage") {
        this.navCtrl.popTo(element);
      }
    });
  }

  updateNote() {
    this.http
      .post("https://realm-glory.herokuapp.com/api/note/update", {
        title: this.note.title,
        preacher: this.note.preacher,
        content: this.content,
        note: this.note._id,
        user: this.note.author
      })
      .subscribe(status => {
        console.log("updated", status);
        this.storage.set("notes", status["data"]);
      });
  }
}



